.menu {
    position: fixed;
    width: 240px;
    top: 56px;
    padding: 12px 12px 12px 0;
    box-sizing: border-box;
    background:#fff;
}
.link {
    display: flex;
    height: 40px;
    padding: 0 24px;
    background-color: white;
    color: black;
    font-size: 14px;
    text-decoration: none;
    align-items: center;
}
.link:hover,
.active{
    background-color: #ededed;
}
.icon {
    width: 24px;
    height: 24px;
    margin-right: 24px;
}
@media screen and (max-width: 911px) {
    .text {
      display: none;
    }
    .menu {
      width: 72px;
      padding: 0;
    }
    .link {
      height: 72px;
    }
    .icon {
      margin: 0;
    }
}