.wrap{
  display: flex;
  flex-direction: column;
  padding-top:56px;
  min-height: 100vh;
  box-sizing: border-box;
}
.container{
  display: flex;
  flex:1;
}
.contents {
  flex: 1;
  /* background-color:#f1f1f1; */
  box-sizing: border-box;
}
.contents.indented{
  margin-left: 240px;
  
}
.tags{
  padding:10px;
  border-bottom:1px solid #ddd;
}
.tag-list{
  display: flex;
}
.tag-list li{
  display: flex;
  align-items: center;
  font-size:14px;
  height: 32px;
  margin-right:4px;
  min-width: 12px;
  padding: 0 10px;
  overflow: hidden;
  background-color:#f3f3f3;
  border:1px solid #ddd;
  border-radius: 16px;
  box-sizing: border-box;
  cursor: pointer;
}
.tag-list li.active{
  background-color:#222;
  border-color:#222;
  color:#fff;
}
@media screen and (max-width: 911px) {
  .contents.indented{
    margin-left: 72px;
  }
}